import React from 'react';

const TextArticle = () => (
  <div className="container">
    <div className="article">
      <div className="article-header">
        <h2> Erstmal Ton an und oben auf Play drcken!</h2>
        <p className='colored-text'>
          Sooooo, ich wnsche dir alles alles Liebe zum Geburtstag Budi  Genie den Tag heute mit Familie und Freunden, den du dir nach diesen stressigen Wochen auf der Arbeit auch wirklich verdient hast! Bin sehr stolz wie toll du das alles gemeistert hast 
          <br/>
          <br/>
          Ich finde, das Jahr 2024 war eines unserer schnsten Jahre zusammen. Wir hatten so viele schne Momente und Erlebnisse, angefangen natrlich mit dem Kauf des Busses, den Trip nach Schweden damit, den Tenero-Urlaub, diverse kleine Ausflge und Besuche bei Freunden und Familie (Samu ), Das Fest, Hazel Brugger, Partys in Fridas Pier und und und 
          <br/>
          <br/>
          Und das Beste ist: Nchstes Jahr kann eigentlich nur noch besser werden, weil wir ganze 3 Monate wegfahren!!! 
          Ich bin so froh dass wir es gewagt haben den Bus zu kaufen. Ohne dich htte ich das vermutlich nie gemacht :) Ich kann es kaum erwarten!
          <br/>
          <br/>
          So, und nun zum wichtigsten Part heute: Deine Geschenke! Und die findest du gleich hier unteeeen. Ich liebe dich! 
        </p>
        <h2>
        
        </h2>
        <p></p>
      </div>
    </div>
  </div>
);

export default TextArticle;


